<script>
    import PropList from './PropList.svelte';
    export let selected = false;
    export let element = {};


    // $:elementFiltered = {

    // }
    function elementFiltered() {
        delete element._id;
        delete element.name;
        return element;
    }
</script>

<section>
    {#if Object.keys(element).length}
        <PropList list={elementFiltered()} />
    {:else}
        {#if selected}
            <h2>click an element to set as reference for comparison</h2>
        {:else}
            <h2>the infos of the hovered element</h2>
        {/if}
    {/if}
</section>

