<script>
    export let list = {};
</script>

{#if Object.keys(list).length }

    {#each Object.keys(list).sort() as prop}
<details>
    <summary>{prop}</summary>
        {#if typeof list[prop] === 'object'}
            <p><svelte:self list={list[prop]} /></p>
        {:else}
            <p>{list[prop]}</p>
        {/if}
</details>
    {/each}
{/if}


