<script>
    import Table from './Table.svelte';
    import TableFilters from './TableFilters.svelte';
    import ElementInfo from './ElementInfo.svelte';

    import { selectedElement, hoveredElement } from './stores.js';

    export let elements;
    let elementsById = {};

    // Grid 18 columns * 7 rows   +  lanthanides/actinides : 15 * 2  (1 row of separation)
    let table = [];
    for (var i = 0; i <= 10; i++) {
        table[i] = Array(18);
    }
    for (let i in elements) {
        let el = elements[i];
        elementsById[el._id["$oid"]] = el;
        if ("periodic_table" in el) {
            let ptable = el.periodic_table;
            // let col = /\d+/.(ptable.group);
            let col = parseInt(ptable.group.replace( new RegExp("\\ *group *","gm"),""));
            let row = ptable.period;
            table[row][col] = el;
            // console.log(row, col);
        } else {
            console.log(el.name);
        }
    }
    // console.log(elementsById);

    $: elementHovered = elementsById[$hoveredElement]
    $: elementSelected = elementsById[$selectedElement]

</script>

<app>
    <main>
        <h1>Periodic Table of Elements</h1>
        <Table table={table} />
    </main>
    <aside>
        <ElementInfo element={elementHovered}/>
        <ElementInfo element={elementSelected} selected/>
    </aside>
    <TableFilters />
    <footer>Made by Dallgoot</footer>
</app>

