    }
    body {
        background-color: #222;
    }
    app {
        display:grid;
        grid-template-areas:
            "tab inf"
            "filter inf"
            "footer footer";
        grid-template-columns: 7fr 30%;
        grid-template-rows: 70% 100px 1em;
        grid-gap: 0px;
        background-color: #222;
    }
    main{
        grid-area: tab;
        overflow:hidden;
        /*background-color: #777;*/
        width: 100%;
        height: 100%;
    }
    aside {
        grid-area: inf;
        background-color: #bbb;
        display: inline-grid;
        grid-template: "hover selected" 5em;
        grid-gap: 0;
    }
/*    aside > section {
        overflow: scroll;
    }
*/
    aside > section:first-child {
        grid-area: hover;
    }
    aside > section:nth-child(2) {
        grid-area: selected;
    }
    body > section:first-child {
        grid-area: filter;
    }
    footer {
        grid-area: footer;
        text-align: center;
        font-size: 0.7em;
        vertical-align: center;
    }
